'use server';
/**
 * @fileOverview An AI agent that generates trading signals and market analysis.
 *
 * - generateTradingSignal - A function that generates trading signals and market analysis.
 * - GenerateTradingSignalInput - The input type for the generateTradingSignal function.
 * - GenerateTradingSignalOutput - The return type for the generateTradingSignal function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';
import { getKlineData as fetchKlineData, getOrderBookData as fetchOrderBookData, getIndicatorData as fetchIndicatorData, getMarketData as fetchMarketData, analyzeOrderBook as anlyzeOrderBookUtil } from '@/lib/actions';
import { getRecentTrades as fetchRecentTradesData, KlineEntrySchema, OrderBookSchema, RecentTradeSchema, TickerInfoSchema, type OrderBook, type TickerInfo, type KlineEntry } from '@/lib/bybit-api';
import { IndicatorSettings, IndicatorDataSchema } from '@/lib/indicators';


const GenerateTradingSignalInputSchema = z.object({
  symbol: z.string().describe('The trading symbol (e.g., BTCUSDT).'),
  timeframe: z.string().describe('The timeframe for the chart and data (e.g., 1m, 5m, 1h, 1d).'),
  indicatorSettings: IndicatorSettings.optional().describe('The settings for the technical indicators.'),
});
export type GenerateTradingSignalInput = z.infer<typeof GenerateTradingSignalInputSchema>;

const GenerateTradingSignalOutputSchema = z.object({
  currentPrice: z.number().describe('The current price of the asset.'),
  entryPrice: z.number().describe('The recommended entry price for the trade.'),
  takeProfit: z.number().describe('The recommended take-profit price level.'),
  stopLoss: z.number().describe('The recommended stop-loss price level.'),
  signal: z.enum(['Buy', 'Sell', 'Hold']).describe('The trading signal: Buy, Sell, or Hold.'),
  reasoning: z.string().describe('Detailed reasoning for the generated signal, based on market analysis.'),
  confidenceLevel: z.enum(['High', 'Medium', 'Low']).describe('The confidence level of the signal.'),
});
export type GenerateTradingSignalOutput = z.infer<typeof GenerateTradingSignalOutputSchema>;

// Tool Definitions
const getKlineData = ai.defineTool({ name: 'getKlineData', description: 'Get candlestick (Kline) data.', inputSchema: z.object({ symbol: z.string(), timeframe: z.string() }), outputSchema: z.array(KlineEntrySchema).nullable() }, fetchKlineData);
const getOrderBookData = ai.defineTool({ name: 'getOrderBookData', description: 'Get order book data.', inputSchema: z.object({ symbol: z.string() }), outputSchema: OrderBookSchema.nullable() }, fetchOrderBookData);
const getRecentTradesData = ai.defineTool({ name: 'getRecentTradesData', description: 'Get recent public trades.', inputSchema: z.object({ symbol: z.string() }), outputSchema: z.array(RecentTradeSchema) }, fetchRecentTradesData);
const getIndicatorData = ai.defineTool({ name: 'getIndicatorData', description: 'Get technical indicator data.', inputSchema: z.object({ symbol: z.string(), timeframe: z.string(), settings: IndicatorSettings.optional() }), outputSchema: IndicatorDataSchema.nullable() }, fetchIndicatorData);
const getMarketData = ai.defineTool({ name: 'getMarketData', description: 'Get the latest market ticker data.', inputSchema: z.object({ symbol: z.string() }), outputSchema: TickerInfoSchema.nullable() }, fetchMarketData);
const analyzeOrderBook = ai.defineTool({ name: 'analyzeOrderBook', description: 'Analyze order book to find support/resistance.', inputSchema: OrderBookSchema, outputSchema: z.object({ support: z.array(z.number()), resistance: z.array(z.number()) }) }, anlyzeOrderBookUtil);

export async function generateTradingSignal(input: z.infer<typeof GenerateTradingSignalInputSchema>): Promise<z.infer<typeof GenerateTradingSignalOutputSchema>> {
  return generateTradingSignalFlow(input);
}

const tradingSignalPrompt = ai.definePrompt({
  name: 'tradingSignalPrompt',
  output: { format: 'json', schema: GenerateTradingSignalOutputSchema },
  tools: [getKlineData, getOrderBookData, getRecentTradesData, getIndicatorData, getMarketData, analyzeOrderBook],
  system: `You are an expert financial analyst AI. Your goal is to generate a trading signal based on a comprehensive analysis of market data.

  **Process:**
  1.  **Get Current Price:** Start by calling \`getMarketData\` to get the current price. This is essential. If it fails, you cannot proceed.
  2.  **Gather Data:** Call all other available data tools (\`getKlineData\`, \`getOrderBookData\`, \`getRecentTradesData\`, \`getIndicatorData\`) to build a complete picture of the market.
  3.  **Analyze Order Book:** If \`getOrderBookData\` was successful, immediately pass its output to \`analyzeOrderBook\` to identify key support and resistance levels.
  4.  **Synthesize Findings:** Review all the data you have gathered. Look for confirmations or divergences between different data sources (e.g., does the RSI confirm the price action? Does the order book support a potential breakout?).
  5.  **Formulate Reasoning:** Construct a detailed, step-by-step \`reasoning\` for your final decision. Reference specific data points (e.g., "RSI is 78, indicating overbought conditions," "Strong resistance identified at $52,000 by order book analysis"). If a tool failed, you MUST mention it in your reasoning (e.g., "Could not retrieve indicator data, so confidence is lowered.").
  6.  **Generate Signal:** Based on your reasoning, determine the \`signal\` (Buy, Sell, or Hold), \`entryPrice\`, \`takeProfit\`, and \`stopLoss\`.
  7.  **Set Confidence:** Determine the \`confidenceLevel\` based on how much data you could retrieve and how strongly it points to a particular outcome.

  Your final output must be a valid JSON object matching the provided schema.`,
});

const generateTradingSignalFlow = ai.defineFlow({ name: 'generateTradingSignalFlow', inputSchema: GenerateTradingSignalInputSchema, outputSchema: GenerateTradingSignalOutputSchema }, async (input) => {
    const response = await tradingSignalPrompt({ prompt: `Generate a trading signal for ${input.symbol} on the ${input.timeframe} timeframe. Indicator settings: ${JSON.stringify(input.indicatorSettings || {})}` });
    
    if (!response.output) {
      throw new Error("AI failed to produce a valid response.");
    }
    const output = response.output;

    // Final validation
    if (output.currentPrice === null || isNaN(output.currentPrice) || output.currentPrice <= 0) {
        throw new Error("AI failed to generate a valid signal with a positive current price.");
    }
    
    const requiredNumericFields: (keyof z.infer<typeof GenerateTradingSignalOutputSchema>)[] = ['entryPrice', 'takeProfit', 'stopLoss'];
    for(const field of requiredNumericFields) {
        if(output[field] === null || isNaN(output[field] as number) || (output[field] as number) <= 0) {
            throw new Error(`AI generated an invalid or non-positive value for '${field}'.`);
        }
    }
    
    return output;
});
