
import { z } from 'zod';
import * as ti from 'technicalindicators';
import { Supertrend } from './supertrend';
import { EhlersInstantaneousTrendline } from 'technicalindicators/lib/trend/EhlersInstantaneousTrendline';
import { FisherTransform } from 'technicalindicators/lib/oscillators/FisherTransform';


// #region Indicator Settings Schemas
export const IndicatorSettings = z.object({
  rsi: z.object({ period: z.number().int().positive().default(14), overbought: z.number().default(70), oversold: z.number().default(30) }),
  macd: z.object({
    fast: z.number().int().positive().default(12),
    slow: z.number().int().positive().default(26),
    signal: z.number().int().positive().default(9),
  }),
  bollingerBands: z.object({
    period: z.number().int().positive().default(20),
    stdDev: z.number().positive().default(2),
  }),
  stochastic: z.object({
    period: z.number().int().positive().default(14),
    slowing: z.number().int().positive().default(3),
    overbought: z.number().default(80),
    oversold: z.number().default(20),
  }),
  atr: z.object({ period: z.number().int().positive().default(14) }),
  williamsR: z.object({ period: z.number().int().positive().default(14), overbought: z.number().default(-20), oversold: z.number().default(-80) }),
  cci: z.object({ period: z.number().int().positive().default(20), overbought: z.number().default(100), oversold: z.number().default(-100) }),
  roc: z.object({ period: z.number().int().positive().default(12) }),
  mfi: z.object({ period: z.number().int().positive().default(14), overbought: z.number().default(80), oversold: z.number().default(20) }),
  awesomeOscillator: z.object({
    fast: z.number().int().positive().default(5),
    slow: z.number().int().positive().default(34),
  }),
  ichimokuCloud: z.object({
    tenkan: z.number().int().positive().default(9),
    kijun: z.number().int().positive().default(26),
    senkou: z.number().int().positive().default(52),
  }),
  sma: z.object({ period: z.number().int().positive().default(20) }),
  supertrendFast: z.object({
    atrPeriod: z.number().int().positive().default(10),
    multiplier: z.number().positive().default(2),
  }),
  supertrendSlow: z.object({
    atrPeriod: z.number().int().positive().default(20),
    multiplier: z.number().positive().default(4),
  }),
  fisher: z.object({ period: z.number().int().positive().default(9) }),
  ehlers: z.object({ period: z.number().int().positive().default(20) }),
});
export type IndicatorSettings = z.infer<typeof IndicatorSettings>;

export const defaultIndicatorSettings: IndicatorSettings = IndicatorSettings.parse({});
// #endregion

// #region Result Schemas
const RsiResultSchema = z.object({ rsi: z.number().nullable() }).nullable();
const MacdResultSchema = z.object({ macd: z.number().nullable(), signal: z.number().nullable(), histogram: z.number().nullable() }).nullable();
const BollingerBandsResultSchema = z.object({ upper: z.number(), middle: z.number(), lower: z.number() }).nullable();
const StochasticResultSchema = z.object({ k: z.number().nullable(), d: z.number().nullable() }).nullable();
const AtrResultSchema = z.object({ atr: z.number().nullable() }).nullable();
const ObvResultSchema = z.object({ obv: z.number().nullable() }).nullable();
const WilliamsRResultSchema = z.object({ williamsR: z.number().nullable() }).nullable();
const CciResultSchema = z.object({ cci: z.number().nullable() }).nullable();
const RocResultSchema = z.object({ roc: z.number().nullable() }).nullable();
const MfiResultSchema = z.object({ mfi: z.number().nullable() }).nullable();
const AwesomeOscillatorResultSchema = z.object({ ao: z.number().nullable() }).nullable();
const IchimokuCloudResultSchema = z.object({ tenkanSen: z.number(), kijunSen: z.number(), senkouSpanA: z.number(), senkouSpanB: z.number() }).nullable();
const SmaResultSchema = z.object({ sma: z.number().nullable() }).nullable();
const SupertrendResultSchema = z.object({ direction: z.enum(['buy', 'sell']), supertrend: z.number() }).nullable();
const FisherTransformResultSchema = z.object({ fisher: z.number().nullable(), trigger: z.number().nullable() }).nullable();
const EhlersTrendlineResultSchema = z.object({ trendline: z.number().nullable() }).nullable();
// #endregion

export const IndicatorDataSchema = z.object({
    rsi: RsiResultSchema,
    macd: MacdResultSchema,
    bollingerBands: BollingerBandsResultSchema,
    stochastic: StochasticResultSchema,
    atr: AtrResultSchema,
    obv: ObvResultSchema,
    williamsR: WilliamsRResultSchema,
    cci: CciResultSchema,
    roc: RocResultSchema,
    mfi: MfiResultSchema,
    awesomeOscillator: AwesomeOscillatorResultSchema,
    ichimokuCloud: IchimokuCloudResultSchema,
    sma: SmaResultSchema,
    supertrendFast: SupertrendResultSchema,
    supertrendSlow: SupertrendResultSchema,
    fisher: FisherTransformResultSchema,
    ehlers: EhlersTrendlineResultSchema,
});

export type IndicatorData = z.infer<typeof IndicatorDataSchema>;


const safeLast = <T>(arr: T[] | undefined): T | null => {
  if (!arr || arr.length === 0) return null;
  return arr[arr.length - 1];
};

/**
 * Calculates a suite of technical indicators based on provided market data.
 * This function is designed to be robust, handling potential calculation errors
 * and insufficient data by returning null for any indicator that cannot be computed.
 *
 * @param closePrices - An array of closing prices, with the most recent price last.
 * @param highPrices - An array of high prices for the corresponding periods.
 * @param lowPrices - An array of low prices for the corresponding periods.
 * @param volumes - An array of trading volumes for the corresponding periods.
 * @param settings - An object containing the parameters for each indicator calculation.
 * @returns An `IndicatorData` object containing the latest value for each indicator.
 */
export function calculateIndicators(
  closePrices: number[],
  highPrices: number[],
  lowPrices: number[],
  volumes: number[],
  settings: IndicatorSettings = defaultIndicatorSettings
): IndicatorData | null {
    if (closePrices.length === 0) {
        return null;
    }

    const input = {
        high: highPrices,
        low: lowPrices,
        close: closePrices,
        volume: volumes,
        period: 0, // Placeholder, will be overridden by specific settings
    };

    // RSI
    const rsiResult = safeLast(ti.RSI.calculate({ values: closePrices, period: settings.rsi.period }));
    const rsi = (rsiResult !== null && !isNaN(rsiResult)) ? { rsi: rsiResult } : null;

    // MACD
    const macdResult = safeLast(ti.MACD.calculate({ 
        values: closePrices,
        fastPeriod: settings.macd.fast,
        slowPeriod: settings.macd.slow,
        signalPeriod: settings.macd.signal,
        SimpleMAOscillator: false,
        SimpleMASignal: false
    }));
    const macd = macdResult && macdResult.MACD !== undefined ? { macd: macdResult.MACD, signal: macdResult.signal, histogram: macdResult.histogram } : null;

    // Bollinger Bands
    const bbResult = safeLast(ti.BollingerBands.calculate({ values: closePrices, period: settings.bollingerBands.period, stdDev: settings.bollingerBands.stdDev }));
    const bollingerBands = bbResult ? { upper: bbResult.upper, middle: bbResult.middle, lower: bbResult.lower } : null;

    // Stochastic
    const stochasticResult = safeLast(ti.Stochastic.calculate({ ...input, period: settings.stochastic.period, signalPeriod: settings.stochastic.slowing }));
    const stochastic = stochasticResult ? { k: stochasticResult.k, d: stochasticResult.d } : null;

    // ATR
    const atrResult = safeLast(ti.ATR.calculate({ ...input, period: settings.atr.period }));
    const atr = (atrResult !== null && !isNaN(atrResult)) ? { atr: atrResult } : null;

    // OBV (On-Balance Volume)
    const obvResult = safeLast(ti.OBV.calculate(input));
    const obv = (obvResult !== null && !isNaN(obvResult)) ? { obv: obvResult } : null;

    // Williams %R
    const williamsRResult = safeLast(ti.WilliamsR.calculate({ ...input, period: settings.williamsR.period }));
    const williamsR = (williamsRResult !== null && !isNaN(williamsRResult)) ? { williamsR: williamsRResult } : null;

    // CCI
    const cciResult = safeLast(ti.CCI.calculate({ ...input, period: settings.cci.period }));
    const cci = (cciResult !== null && !isNaN(cciResult)) ? { cci: cciResult } : null;
    
    // ROC
    const rocResult = safeLast(ti.ROC.calculate({ values: closePrices, period: settings.roc.period }));
    const roc = (rocResult !== null && !isNaN(rocResult)) ? { roc: rocResult } : null;

    // MFI
    const mfiResult = safeLast(ti.MFI.calculate({ ...input, period: settings.mfi.period }));
    const mfi = (mfiResult !== null && !isNaN(mfiResult)) ? { mfi: mfiResult } : null;

    // Awesome Oscillator
    const aoResult = safeLast(ti.AwesomeOscillator.calculate({ ...input, fastPeriod: settings.awesomeOscillator.fast, slowPeriod: settings.awesomeOscillator.slow, format: (a) => a }));
    const awesomeOscillator = (aoResult !== null && !isNaN(aoResult)) ? { ao: aoResult } : null;

    // Ichimoku Cloud
    const icResult = safeLast(ti.IchimokuCloud.calculate({ 
        high: highPrices,
        low: lowPrices,
        conversionPeriod: settings.ichimokuCloud.tenkan,
        basePeriod: settings.ichimokuCloud.kijun,
        spanPeriod: settings.ichimokuCloud.senkou,
        displacement: settings.ichimokuCloud.senkou // In library, displacement is same as span B
    }));
    const ichimokuCloud = icResult ? { tenkanSen: icResult.conversion, kijunSen: icResult.base, senkouSpanA: icResult.spanA, senkouSpanB: icResult.spanB } : null;

    // SMA
    const smaResult = safeLast(ti.SMA.calculate({ values: closePrices, period: settings.sma.period }));
    const sma = (smaResult !== null && !isNaN(smaResult)) ? { sma: smaResult } : null;
    
    // Supertrend
    const stFastResult = safeLast(Supertrend.calculate({ ...input, period: settings.supertrendFast.atrPeriod, multiplier: settings.supertrendFast.multiplier }));
    const supertrendFast = stFastResult ? { direction: stFastResult.direction > 0 ? 'buy' : 'sell', supertrend: stFastResult.value } : null;
    
    const stSlowResult = safeLast(Supertrend.calculate({ ...input, period: settings.supertrendSlow.atrPeriod, multiplier: settings.supertrendSlow.multiplier }));
    const supertrendSlow = stSlowResult ? { direction: stSlowResult.direction > 0 ? 'buy' : 'sell', supertrend: stSlowResult.value } : null;

    // Fisher Transform
    const fisherResult = safeLast(FisherTransform.calculate({ ...input, period: settings.fisher.period }));
    const fisher = fisherResult ? { fisher: fisherResult.fisher, trigger: fisherResult.trigger } : null;

    // Ehlers Instantaneous Trendline
    const ehlersResult = safeLast(EhlersInstantaneousTrendline.calculate({ values: closePrices, period: settings.ehlers.period }));
    const ehlers = (ehlersResult !== null && !isNaN(ehlersResult)) ? { trendline: ehlersResult } : null;

    return {
        rsi,
        macd,
        bollingerBands,
        stochastic,
        atr,
        obv,
        williamsR,
        cci,
        roc,
        mfi,
        awesomeOscillator,
        ichimokuCloud,
        sma,
        supertrendFast,
        supertrendSlow,
        fisher,
        ehlers,
    };
}
