
import { Indicator, IndicatorInput } from 'technicalindicators/lib/indicator/indicator';
import { ATR } from 'technicalindicators';

export class SupertrendInput extends IndicatorInput {
  multiplier: number;
  period: number;
  high: number[];
  low: number[];
  close: number[];
}

export class Supertrend extends Indicator {
  result: any;
  generator: IterableIterator<any>;
  constructor(input: SupertrendInput) {
    super(input);
    const atr = new ATR({
      high: [],
      low: [],
      close: [],
      period: input.period,
    });

    this.result = [];

    this.generator = (function* () {
      let currentTick = yield;
      let lastResult;
      let atrValue;
      while (true) {
        atrValue = atr.nextValue({
          high: currentTick.high,
          low: currentTick.low,
          close: currentTick.close,
        });
        if (atrValue === undefined) {
          currentTick = yield;
          continue;
        }

        const up = (currentTick.high + currentTick.low) / 2 - input.multiplier * atrValue;
        const down = (currentTick.high + currentTick.low) / 2 + input.multiplier * atrValue;
        
        const result = {
            up: up,
            down: down,
            direction: 1,
            value: 0
        };

        if (lastResult === undefined) {
          result.value = result.up;
          currentTick = yield result;
          lastResult = result;
          continue;
        }

        result.up = lastResult.up;
        result.down = lastResult.down;
        result.direction = lastResult.direction;

        if (lastResult.value === lastResult.up && currentTick.close < lastResult.up) {
            result.direction = -1;
            result.value = down;
        } else if (lastResult.value === lastResult.down && currentTick.close > lastResult.down) {
            result.direction = 1;
            result.value = up;
        } else if (result.direction === 1) {
            result.value = Math.max(up, lastResult.up);
        } else {
            result.value = Math.min(down, lastResult.down);
        }

        if (result.direction === 1) {
          result.up = result.value;
        } else {
          result.down = result.value;
        }
        
        lastResult = result;
        currentTick = yield result;
      }
    })();

    this.generator.next();

    input.low.forEach((tick, index) => {
      const result = this.generator.next({
        low: input.low[index],
        high: input.high[index],
        close: input.close[index],
      }).value;
      if (result) this.result.push(result);
    });
  }

  static calculate(input: SupertrendInput): any {
    Indicator.reverseInputs(input);
    const result = new Supertrend(input).result;
    if (input.reversedInput) {
      result.reverse();
    }
    Indicator.reverseInputs(input);
    return result;
  }

  nextValue(tick: any): any {
    return this.generator.next(tick).value;
  }
}
