'use server';

import { generateTradingSignal, type GenerateTradingSignalOutput } from '@/ai/flows/generate-trading-signal';
import { getTicker, getOrderBook, getKline, type OrderBook, type TickerInfo, type KlineEntry } from '@/lib/bybit-api';
import { calculateIndicators, type IndicatorData, type IndicatorSettings, defaultIndicatorSettings } from '@/lib/indicators';

/**
 * The response shape for the AI trading signal analysis.
 */
export type AiSignalResponse = GenerateTradingSignalOutput;

/**
 * Server action to generate an AI-powered trading signal.
 * This function calls the Genkit flow and handles its response or errors.
 *
 * @param symbol - The trading symbol (e.g., 'BTCUSDT').
 * @param timeframe - The chart timeframe (e.g., '1h').
 * @param indicatorSettings - Optional custom settings for technical indicators.
 * @returns An object containing the success status, the analysis, and an optional error message.
 */
export async function getAiTradingSignal(symbol: string, timeframe: string, indicatorSettings?: IndicatorSettings): Promise<{ success: boolean, analysis?: AiSignalResponse, error?: string }> {
  try {
    const result = await generateTradingSignal({ symbol, timeframe, indicatorSettings });
    return { success: true, analysis: result };
  } catch (error) {
    console.error('Error generating AI trading signal:', error);
    const errorMessage = error instanceof Error ? error.message : 'An unknown error occurred.';
    return { success: false, error: errorMessage };
  }
}

/**
 * Server action to get the latest market data (ticker info) for a symbol.
 *
 * @param symbol - The trading symbol.
 * @returns A promise that resolves to the TickerInfo object or null.
 */
export async function getMarketData(symbol: string): Promise<TickerInfo | null> {
    return getTicker(symbol);
}

/**
 * Server action to get the order book for a symbol.
 *
 * @param symbol - The trading symbol.
 * @returns A promise that resolves to the OrderBook object or null.
 */
export async function getOrderBookData(symbol: string): Promise<OrderBook | null> {
    return getOrderBook(symbol);
}

/**
 * Server action to get Kline (candlestick) data.
 *
 * @param symbol - The trading symbol.
 * @param interval - The timeframe interval (e.g., '1h', '4h').
 * @returns A promise that resolves to an array of Kline entries or null.
 */
export async function getKlineData(symbol: string, interval: string): Promise<KlineEntry[] | null> {
    return getKline(symbol, interval);
}

/**
 * Server action to calculate a suite of technical indicators for a symbol and timeframe.
 *
 * @param symbol - The trading symbol.
 * @param timeframe - The chart timeframe.
 * @param settings - Optional custom indicator settings.
 * @returns A promise that resolves to the calculated IndicatorData object or null.
 */
export async function getIndicatorData(symbol: string, timeframe: string, settings: IndicatorSettings = defaultIndicatorSettings): Promise<IndicatorData | null> {
    const klineData = await getKline(symbol, timeframe);
    if (!klineData) return null;
    const reversedKline = [...klineData].reverse();
    const closePrices = reversedKline.map(k => parseFloat(k[4]));
    const highPrices = reversedKline.map(k => parseFloat(k[2]));
    const lowPrices = reversedKline.map(k => parseFloat(k[3]));
    const volumes = reversedKline.map(k => parseFloat(k[5]));
    return calculateIndicators(closePrices, highPrices, lowPrices, volumes, settings);
}

/**
 * Server action to analyze an order book and identify major support and resistance levels.
 *
 * @param orderBook - The OrderBook object to analyze.
 * @returns A promise resolving to an object with arrays of support and resistance price levels.
 */
export async function analyzeOrderBook(orderBook: OrderBook): Promise<{ support: number[]; resistance: number[] }> {
  const bids = orderBook.bids.map(([price, size]) => ({ price: parseFloat(price), size: parseFloat(size) }));
  const asks = orderBook.asks.map(([price, size]) => ({ price: parseFloat(price), size: parseFloat(size) }));

  // Helper function to find the levels with the largest order sizes
  const findLevels = (orders: { price: number; size: number }[], count: number) => {
    const sorted = [...orders].sort((a, b) => b.size - a.size);
    return sorted.slice(0, count).map(o => o.price);
  };
  
  const supportLevels = findLevels(bids, 3);
  const resistanceLevels = findLevels(asks, 3);

  return {
    support: supportLevels,
    resistance: resistanceLevels,
  };
}
