
import { z } from 'zod';

// #region Zod Schemas and Types
/**
 * Zod schema for Bybit ticker information.
 */
export const TickerInfoSchema = z.object({
  lastPrice: z.string(),
  highPrice24h: z.string(),
  lowPrice24h: z.string(),
  turnover24h: z.string(), // Volume in quote currency (USDT)
  volume24h: z.string(), // Volume in base currency (e.g., BTC)
  price24hPcnt: z.string(), // 24h price change percentage
});
export type TickerInfo = z.infer<typeof TickerInfoSchema>;

/**
 * Zod schema for a single entry in the order book (a price and size pair).
 */
export const OrderBookEntrySchema = z.tuple([z.string(), z.string()]); // [price, size]
export type OrderBookEntry = z.infer<typeof OrderBookEntrySchema>;

/**
 * Zod schema for the entire order book.
 */
export const OrderBookSchema = z.object({
  bids: z.array(OrderBookEntrySchema),
  asks: z.array(OrderBookEntrySchema),
  ts: z.string(), // Orderbook timestamp
});
export type OrderBook = z.infer<typeof OrderBookSchema>;

/**
 * Zod schema for a single recent trade.
 */
export const RecentTradeSchema = z.object({
  execId: z.string(),
  execTime: z.union([z.string(), z.number()]),
  price: z.string(),
  qty: z.string(),
  side: z.enum(['Buy', 'Sell']),
  isBlockTrade: z.boolean().optional(), // Whether it's a block trade
});
export type RecentTrade = z.infer<typeof RecentTradeSchema>;

/**
 * Zod schema for a single Kline (candlestick) entry.
 */
export const KlineEntrySchema = z.tuple([
  z.string(), // Start time
  z.string(), // Open
  z.string(), // High
  z.string(), // Low
  z.string(), // Close
  z.string(), // Volume
  z.string(), // Turnover
  z.string().optional() // Optional: Confirm flag (1 if the kline is closed)
]);
export type KlineEntry = z.infer<typeof KlineEntrySchema>;

// #endregion

// #region Constants
const BYBIT_API_URL = 'https://api.bybit.com';
export const BYBIT_WEBSOCKET_URL = 'wss://stream.bybit.com/v5/public/spot';
export const DEFAULT_REQUEST_TIMEOUT = 10000; // 10 seconds
const MAX_RETRIES = 3;
const RETRY_DELAY_MS = 1000;
// #endregion

/**
 * The standard response structure from the Bybit API.
 */
interface ApiResponse<T> {
  retCode: number;
  retMsg: string;
  result: T;
  time: number;
}

/**
 * An enhanced fetch function for interacting with the Bybit API.
 * Includes retry logic, request timeout, and standardized error handling.
 *
 * @param endpoint - The API endpoint path (e.g., '/v5/market/tickers').
 * @param params - A record of query parameters to include in the request.
 * @param options - Standard RequestInit options to pass to fetch.
 * @returns A promise that resolves to the 'result' field of the API response, or null if an error occurs.
 */
async function fetchFromBybit<T>(
  endpoint: string, 
  params: Record<string, string> = {}, 
  options: RequestInit = {}
): Promise<T | null> {
  const url = new URL(endpoint, BYBIT_API_URL);
  url.search = new URLSearchParams(params).toString();
  
  const controller = new AbortController();
  const timeoutId = setTimeout(() => controller.abort(), DEFAULT_REQUEST_TIMEOUT);
  
  const fetchOptions: RequestInit = {
    cache: 'no-store',
    signal: options.signal || controller.signal, // Use provided signal or internal timeout signal
    headers: {
      'Content-Type': 'application/json',
    },
    ...options
  };

  let retryCount = 0;
  
  while (retryCount <= MAX_RETRIES) {
    try {
      const response = await fetch(url.toString(), fetchOptions);
      clearTimeout(timeoutId); // Clear timeout once response is received
      
      if (!response.ok) {
        throw new Error(`Bybit API error: ${response.status} ${response.statusText}`);
      }
      
      const data: ApiResponse<T> = await response.json();
      
      if (data.retCode !== 0) {
        throw new Error(`Bybit API error: ${data.retMsg} (Code: ${data.retCode})`);
      }
      
      return data.result;
    } catch (error) {
      clearTimeout(timeoutId);
      
      // Don't retry on abort errors or if we've reached max retries
      if (error instanceof Error && error.name === 'AbortError') {
        console.error(`Request to ${endpoint} timed out or was aborted`);
        return null;
      }
      
      if (retryCount === MAX_RETRIES) {
        console.error(`Failed to fetch from Bybit endpoint ${endpoint} after ${MAX_RETRIES} retries:`, error);
        return null;
      }
      
      // Exponential backoff
      const delay = RETRY_DELAY_MS * Math.pow(2, retryCount);
      console.warn(`Retry ${retryCount + 1}/${MAX_RETRIES} for ${endpoint} in ${delay}ms`);
      
      await new Promise(resolve => setTimeout(resolve, delay));
      retryCount++;
    }
  }
  
  return null;
}

/**
 * Fetches ticker information for a specific symbol.
 *
 * @param symbol - The trading symbol (e.g., 'BTCUSDT').
 * @returns A promise resolving to a validated TickerInfo object or null.
 */
export async function getTicker(symbol: string): Promise<TickerInfo | null> {
  try {
    const result = await fetchFromBybit<any>('/v5/market/tickers', { 
      category: 'spot', 
      symbol 
    });
    
    if (!result || !result.list || result.list.length === 0) {
      console.warn(`No ticker data found for symbol: ${symbol}`);
      return null;
    }
    
    // Extract and validate the ticker data
    const ticker = result.list[0];
    const validation = TickerInfoSchema.safeParse(ticker);

    if(!validation.success) {
        console.warn(`Invalid ticker data received for ${symbol}:`, validation.error);
        return null;
    }
    
    return validation.data;

  } catch (error) {
    console.error(`Error fetching ticker for ${symbol}:`, error);
    return null;
  }
}

/**
 * Fetches the order book for a specific symbol.
 *
 * @param symbol - The trading symbol.
 * @param limit - The number of price levels to fetch (depth).
 * @returns A promise resolving to a validated OrderBook object or null.
 */
export async function getOrderBook(symbol: string, limit: number = 20): Promise<OrderBook | null> {
  try {
    const result = await fetchFromBybit<any>('/v5/market/orderbook', { 
      category: 'spot', 
      symbol, 
      limit: limit.toString() 
    });
    
    if (!result) {
      console.warn(`No orderbook data found for symbol: ${symbol}`);
      return null;
    }
    
    const validation = OrderBookSchema.safeParse({
        bids: result.b,
        asks: result.a,
        ts: result.ts?.toString() || Date.now().toString()
    });

    if(!validation.success) {
        console.warn(`Invalid orderbook data for ${symbol}:`, validation.error);
        return null;
    }
    
    return validation.data;

  } catch (error) {
    console.error(`Error fetching orderbook for ${symbol}:`, error);
    return null;
  }
}

/**
 * Fetches the most recent public trades for a symbol.
 *
 * @param symbol - The trading symbol.
 * @param limit - The maximum number of trades to fetch.
 * @param options - Standard RequestInit options to pass to fetch.
 * @returns A promise resolving to an array of validated RecentTrade objects.
 */
export async function getRecentTrades(
  symbol: string, 
  limit: number = 30, 
  options: RequestInit = {}
): Promise<RecentTrade[]> {
  try {
    const result = await fetchFromBybit<any>(
      '/v5/market/recent-trade', 
      { 
        category: 'spot', 
        symbol, 
        limit: limit.toString() 
      }, 
      options
    );
    
    if (!result || !result.list || !Array.isArray(result.list)) {
      return [];
    }
    
    // Filter and validate trade data
    const validatedTrades: RecentTrade[] = [];
    for(const trade of result.list) {
        const mappedTrade = {
            execId: trade.i,
            execTime: trade.T,
            price: trade.p,
            qty: trade.v,
            side: trade.S,
            isBlockTrade: trade.m,
        };
        const validation = RecentTradeSchema.safeParse(mappedTrade);
        if(validation.success) {
            validatedTrades.push(validation.data);
        }
    }
    return validatedTrades;

  } catch (error) {
    if (!(error instanceof Error && error.name === 'AbortError')) {
      console.error(`Error fetching recent trades for ${symbol}:`, error);
    }
    return [];
  }
}

/**
 * Fetches Kline (candlestick) data for a symbol and interval.
 *
 * @param symbol - The trading symbol.
 * @param interval - The user-friendly interval string (e.g., '1h', '1d').
 * @param limit - The number of candlesticks to fetch.
 * @returns A promise resolving to an array of validated KlineEntry tuples or null.
 */
export async function getKline(
  symbol: string, 
  interval: string, 
  limit: number = 100
): Promise<KlineEntry[] | null> {
  try {
    // Map user-friendly intervals to Bybit's required format
    const intervalMap: Record<string, string> = {
      '1m': '1',
      '5m': '5',
      '15m': '15',
      '30m': '30',
      '1h': '60',
      '2h': '120',
      '4h': '240',
      '6h': '360',
      '12h': '720',
      '1d': 'D',
      '1w': 'W',
      '1M': 'M',
    };
    
    const bybitInterval = intervalMap[interval] || interval;
    
    const result = await fetchFromBybit<any>('/v5/market/kline', { 
      category: 'spot', 
      symbol, 
      interval: bybitInterval, 
      limit: limit.toString() 
    });
    
    if (!result || !result.list || !Array.isArray(result.list)) {
      console.warn(`No kline data found for symbol: ${symbol} with interval: ${interval}`);
      return null;
    }
    
    const validation = z.array(KlineEntrySchema).safeParse(result.list);
    if (!validation.success) {
        console.warn(`Invalid kline data for ${symbol} with interval ${interval}:`, validation.error);
        return null;
    }

    return validation.data;

  } catch (error) {
    console.error(`Error fetching kline data for ${symbol} with interval ${interval}:`, error);
    return null;
  }
}

/**
 * Fetches ticker information for multiple symbols concurrently.
 *
 * @param symbols - An array of trading symbols.
 * @returns A promise resolving to a record mapping each symbol to its TickerInfo.
 */
export async function getTickers(symbols: string[]): Promise<Record<string, TickerInfo>> {
  const results: Record<string, TickerInfo> = {};
  
  // Use Promise.allSettled to handle partial failures gracefully
  const promises = symbols.map(async (symbol) => {
    const ticker = await getTicker(symbol);
    return { symbol, ticker };
  });
  
  const settledResults = await Promise.allSettled(promises);
  
  settledResults.forEach((result) => {
    if (result.status === 'fulfilled' && result.value.ticker) {
      const { symbol, ticker } = result.value;
      results[symbol] = ticker;
    }
  });
  
  return results;
}
